<template>
  <footer class="footer">
    <Text class="footer__title">
      Feral Technologies
    </Text>
    <div class="footer__location">
      <Icon name="uae"/>
      <Text>United Arab Emirates</Text>
    </div>
    <div class="footer__hr"/>
    <div class="footer__social">
      <Icon
        v-for="item in socialList"
        :key="item.key"
        :name="item.icon"
        :height="isTablet ? null : '18px'"
        :width="isTablet ? null : '18px'"
        @click="onSocialClick(item)"
      />
    </div>
  </footer>
</template>

<script>
import Text from '@/components/UIKit/Text/Text'
import Icon from '@/components/UIKit/Icon'
import { useBreakpointsMedia } from '@/plugins/breakpoints'
export default {
  name: 'Footer',
  components: {
    Icon,
    Text
  },
  setup () {
    const { isTablet } = useBreakpointsMedia()
    const socialList = [
      {
        key: 'twitter',
        href: 'https://twitter.com',
        icon: 'twitter'
      },
      {
        key: 'instagram',
        href: 'https://www.instagram.com',
        icon: 'instagram'
      },
      {
        key: 'linkedin',
        href: 'https://www.linkedin.com',
        icon: 'linkedin'
      }
    ]

    const onSocialClick = item => {
      window.location.href = item.href
    }

    return {
      isTablet,
      socialList,
      onSocialClick
    }
  }
}
</script>

